/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  height: 100%;
  overflow: auto;
  background-color: #f3f5f8;
}

.container {
  width: 100%;
  min-height: 100vh;
}

img {
  max-width: 100%;
}

nav {
  display: flex;
  width: 100%;
  justify-content: space-around;
  height: 80%;
  line-height: 80px;
  z-index: 111;
}

.logo {
  display: flex;
  font-size: 2.5rem;
  font-weight: 800;
  color: #3d535f;
  letter-spacing: 3px;
  cursor: pointer;
  text-transform: uppercase;
}

h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

b {
  font-size: 4rem;
  color: #7f00ff;
}

.navItems {
  display: flex;
}

.navItems li {
  list-style: none;
  margin: 0 0.9rem;
}

.navItems li a {
  font-size: 2.1rem;
  color: #3d535f;
  letter-spacing: 0.18rem;
  text-decoration: none;
  text-transform: capitalize;
}

.navItems li a:hover,
.links a:hover {
  color: #7f00ff;
}

.links a {
  font-size: 1.8rem;
  color: #3d535f;
  margin: 1.5rem 1.8rem;
}

.wrapper {
  display: flex;
  width: 100%;
  height: calc(100vh - 80px);
  justify-content: center;
  align-items: center;
  padding: 0 10%;
  overflow: hidden;
}

.wrapper::before {
  position: absolute;
  content: "";
  height: 600px;
  width: 600px;
  border-radius: 50%;
  left: -12%;
  top: 40%;
  background: linear-gradient(45deg, #7f00ff, pink);
  animation: object1 6s linear infinite;
}

.wrapper::after {
  position: absolute;
  content: "";
  height: 200px;
  width: 200px;
  border-radius: 50%;
  left: 35%;
  top: 12%;
  background: linear-gradient(45deg, #7f00ff, pink);
  animation: object2 6s linear infinite;
}

@keyframes object1 {
  50% {
    left: -13%;
    top: 41%;
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
@keyframes object2 {
  50% {
    left: 35%;
    top: 10%;
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
.cols {
  width: 50%;
}

.cols0 {
  width: 50%;
  z-index: 5;
}

.topline {
  display: block;
  position: relative;
  font-size: 2.1rem;
  color: #3d535f;
}

.topline::after {
  position: absolute;
  content: "";
  height: 0.25rem;
  width: 2.825rem;
  bottom: 0.6rem;
  background-color: #7f00ff;
}

h1 {
  display: block;
  font-size: 7em;
  font-weight: 900;
  color: #3d535f;
  margin: 1rem;
}

.multiText {
  color: #7f00ff;
  text-transform: capitalize;
}

p {
  display: block;
  width: 90%;
  font-size: 1.2em;
  color: #3d535f;
}

.btns {
  width: 100%;
  position: relative;
  left: 9.375rem;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 400;
  color: #fff;
  background-color: #3d535f;
  padding: 8px 4px;
  margin: 40px 5px;
  letter-spacing: 2px;
  text-transform: capitalize;
  box-shadow: 0 15px 10px rgba(0, 0, 0, 0.4); /*Sombreado*/
}

button:hover {
  background-color: #7f00ff;
}

.imgbox {
  position: relative;
  width: 100%;
  height: 100%;
}

.imgbox img {
  top: 50%;
  left: 50%;
  transform: translate(-10%, -10%) rotate(-25deg);
  width: 160%;
  filter: saturate(200%);
  animation: animate 4s linear infinite;
}

@keyframes animate {
  50% {
    top: 49%;
    left: 51%;
    width: 165%;
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
