  margin-bottom: 40px;
}

.estudios h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #333;
}

.estudios ul {
  font-size: 16px;
  line-height: 1.6;
  list-style: none;
  margin-left: 0;
}

.estudios li {
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
}

.estudios li::before {
