.gustos {
  margin-bottom: 40px;
}

.gustos h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #333;
}

.gustos ul {
  font-size: 16px;
}

/* Estilos para la secci贸n de portafolio */
.portafolio {
  background-color: #f8f8f8;
  padding: 80px 0;
}

.portafolio h2 {
  font-size: 40px;
  text-align: center;
  margin-bottom: 40px;
  color: #333;
}

.portafolio p {
  font-size: 20px;
  text-align: center;
  margin-bottom: 40px;
  color: #666;
}

.lista-portafolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.item-portafolio {
  margin: 20px;
}

.item-portafolio a {
  display: block;
  position: relative;
}

.item-portafolio img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.item-portafolio h3 {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 10px;
  margin: 0;
  font-size: 20px;
  text-align: center;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.item-portafolio a:hover h3 {
  opacity: 1;
}

